/* 设置页面的基础样式 */
.settings-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.settings-section {
    width: 100%;
    max-width: 600px; /* 限制内容最大宽度 */
    margin: 10px 0;
}

.settings-section h2 {
    margin-bottom: 10px;
}

.settings-section div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px 0;
}

.settings-section label {
    flex: 1;
}

.settings-section input,
.settings-section select {
    flex: 2;
    padding: 5px;
    margin-left: 10px;
}

/* 按钮样式 */
.save-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
}

/* 响应式调整 */
@media (max-width: 600px) {
    .settings-section {
        max-width: 100%;
        flex-direction: column;
    }

    .settings-section div {
        flex-direction: column;
        align-items: flex-start;
    }

    .settings-section label,
    .settings-section input,
    .settings-section select {
        width: 100%;
        margin: 5px 0;
    }
}